import styled, { css } from 'styled-components';
import Icon from '../../icon';
import { theme, Colour } from '@fellesdatakatalog/theme'

interface Props {
  $collapsed? : boolean;
  $showAlert : boolean;
}

const Accordion = styled.div<Props>`
  display: flex;
  flex-direction: column;

  min-height: ${props => 
    props.$collapsed
    ? theme.spacing('S96')
    : theme.spacing('S72')
  };
  border-radius: ${theme.spacing('S4')};

  :hover {
    background-color: ${theme.colour(Colour.NEUTRAL, 'N70')};
    h1, h2, h3, h4, h5, h6, p {
      color: ${theme.colour(Colour.NEUTRAL, 'N0')};
    }

    span {
      background-color: ${theme.colour(Colour.NEUTRAL, 'N0')};
      color: ${theme.colour(Colour.NEUTRAL, 'N70')};
    }

    svg {
      & * {
        stroke: ${theme.colour(Colour.NEUTRAL, 'N0')};
      }
    }
  }

  transition: .5s;
  * > {
    transition: .5s;
  }

  ${props => props.$showAlert && css`background-color: ${theme.colour(Colour.RED, 'R30')}`}
`;

const CollapseButton = styled.button`
  display: flex;
  flex-direction: column;
  background-color: transparent;
  font-family: inherit;
  border: none;
  padding: 0px ${theme.spacing('S24')};
  cursor: pointer;
`;

const DivHeading = styled.div`
  display: flex;
  flex-direction: row;
  gap: ${theme.spacing('S12')};
  margin: ${theme.spacing('S24')} 0px 0px;
`;

const AccordionTitleContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: ${theme.spacing('S12')};
  flex: 1;
`;

const AccordionTitle = styled.h2<Props>`
  display: flex;
  justify-self: flex-start;
  align-items: baseline;

  font-weight: ${theme.fontWeight('FW700')};
  font-size: ${theme.fontSize('FS24')};
  line-height: ${theme.spacing('S32')};

  padding: 0px;
  margin: 0px;

  color: ${props =>
    props.$showAlert 
    ? theme.colour(Colour.RED, 'R60')
    : theme.colour(Colour.BLUE, 'B60')
  };
`;

const ChevronIcon = styled(Icon)<Props>`
  align-self: center;
  & * {
    stroke: ${props => 
      props.$showAlert 
      ? css`${theme.colour(Colour.RED, 'R60')}` 
      : `background-color: ${theme.colour(Colour.BLUE, 'B60')};`
    };
  }
`;

const AlertIcon = styled(Icon)`
  align-self: center;
  & * {
      stroke: ${theme.colour(Colour.RED, 'R60')};
  }
`;

const MandatoryTagWrapper = styled.div<Props>`
  span {
    background-color: ${props =>
      props.$showAlert
      ? theme.colour(Colour.RED, 'R60')
      : theme.colour(Colour.BLUE, 'B60')
    };
    color: ${theme.colour(Colour.NEUTRAL, 'N0')};
  }
`;

const RecommendedTagWrapper = styled.div<Props>`
  span {
    color: ${props =>
    props.$showAlert
      ? theme.colour(Colour.RED, 'R60')
      : theme.colour(Colour.BLUE, 'B60')
    };
    background-color: ${props =>
      props.$showAlert
      ? theme.colour(Colour.NEUTRAL, 'N0')
      : theme.colour(Colour.BLUE, 'B30')
    };
  }
`;

const AccordionSummary = styled.p<Props>`
  justify-self: center;
  align-self: flex-start;
  margin: 0px 0px ${theme.spacing('S24')};
  margin: 0px 0px ${theme.spacing('S24')};

  font-weight: ${theme.fontWeight('FW400')};
  font-size: ${theme.fontSize('FS16')};

  line-height: ${theme.spacing('S24')};
  color: ${props =>
    props.$showAlert
    ? theme.colour(Colour.RED, 'R60')
    : theme.colour(Colour.BLUE, 'B60')
  };
`;

export default {
  Accordion,
  CollapseButton,
  DivHeading,
  AccordionTitle,
  AccordionTitleContainer,
  ChevronIcon,
  AlertIcon,
  MandatoryTagWrapper,
  RecommendedTagWrapper,
  AccordionSummary,
};
