import React, { FC } from "react";
import { localization } from "../../../utils/language/localization";
import MandatoryTag from "../../tag/mandatory-tag";
import RecommendedTag from "../../tag/recommended-tag";
import SC from './styled';

interface Props {
  title: string;
  dcatKeyword?: string;
  hasError?: boolean;
  initiallyCollapsed: boolean;
  summary: string;
}

const Header: FC<Props> = ({
  title: title,
  dcatKeyword,
  hasError,
  initiallyCollapsed,
  summary
}) => {
  let collapsed = initiallyCollapsed;

  const showAlertHeader = () => Boolean(hasError && collapsed);

  const ChevronIcon = () => (
    collapsed
      ? <SC.ChevronIcon
        name="chevronDownStroke"
        $showAlert={showAlertHeader()} />
      : <SC.ChevronIcon
        name="chevronUpStroke"
        $showAlert={showAlertHeader()} />
  )

  const AlertIcon = () => (
    hasError
    ? <SC.AlertIcon
      name="triangleExclamationPointStroke" />
    : <></>);

  const DcatKeyword = () => {
    switch (dcatKeyword) {
      case 'mandatory':
        return (
          <SC.MandatoryTagWrapper $showAlert={ showAlertHeader() }>
            <MandatoryTag />
          </SC.MandatoryTagWrapper>
          )
      case 'optional':
        return (
          <SC.RecommendedTagWrapper $showAlert={ showAlertHeader() }>
            <RecommendedTag />
          </SC.RecommendedTagWrapper>
        )
      default:
        return <></>;
    }
  }

  const Summary = () => (
    collapsed ? (
      <SC.AccordionSummary $showAlert={showAlertHeader()}>
        {hasError ? localization.sectionErrorMessage : summary}
      </SC.AccordionSummary>
    ) :
      <></>
  )

  return (
    <SC.Accordion $collapsed={collapsed} $showAlert={showAlertHeader()}>
      <SC.CollapseButton type='button'>
        <>
          <SC.DivHeading>
            <SC.AccordionTitleContainer>
              <SC.AccordionTitle $showAlert={showAlertHeader()}>
                {title}
              </SC.AccordionTitle>
              <DcatKeyword />
            </SC.AccordionTitleContainer>
            <AlertIcon />
            <ChevronIcon />
          </SC.DivHeading>
          <Summary />
        </>
      </SC.CollapseButton>
    </SC.Accordion>
  )
}

export default Header;
