import React, { FC, PropsWithChildren } from "react";
import SC from './styled';

import Header from './header';

interface Props {
  title: string;
  dcatKeyword?: string;
  hasError: boolean;
  summary: string;
  initiallyCollapsed: boolean;
}

const Accordion: FC<PropsWithChildren<Props>> = ({
  title,
  dcatKeyword,
  hasError,
  initiallyCollapsed,
  summary,
  children
}) => {
  return (
      <SC.Accordion>
        <Header
          title={title}
          dcatKeyword={dcatKeyword}
          hasError={hasError}
          summary={summary}
          initiallyCollapsed={initiallyCollapsed}
        />
        {children}
      </SC.Accordion>
  )
}

export default Accordion;
